import React, { useContext, useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './language.css'
import nodejs from './languages/nodejs.png';
import mongodb from './languages/mongodb.png';
import materialui from './languages/materialui.png';
import express from './languages/express.png';

import { themeContext } from '../App';

const FamiliarWith = () => {
    const context = useContext(themeContext)
    const {theme} = context;
    useEffect(() => {
        AOS.init();
        // eslint-disable-next-line
      }, [])
  return (
    <div className='container'> 
        <h2 className={`mb-4 text-center text-${theme==='dark'?'light':'dark'}`}>Familiar With</h2>
        <div className="langContainer py-4" style={{backgroundColor:theme==='dark'?'rgba(207, 207, 207, 0.04)':'rgba(207, 207, 207, 0.494)'}}>
            <div data-aos="fade-up" className="langItem my-3" style={{backgroundColor: theme==='dark'?'rgba(6, 6, 6, 0.4)':'white'}}>
                <img className='language' src={nodejs} alt="loading" />
            </div>
            <div data-aos="fade-up" className="langItem my-3" style={{backgroundColor: theme==='dark'?'rgba(6, 6, 6, 0.4)':'white'}}>
                <img className='language' src={mongodb} alt="loading" />
            </div>
            <div data-aos="fade-up" className="langItem my-3" style={{backgroundColor: theme==='dark'?'rgba(6, 6, 6, 0.4)':'white'}}>
                <img className='language' src={materialui} alt="loading" />
            </div>
            <div data-aos="fade-up" className="langItem my-3" style={{backgroundColor: theme==='dark'?'rgba(6, 6, 6, 0.4)':'white'}}>
                <img className='language' src={express} alt="loading" />
            </div>
        </div>
    </div>
  )
}

export default FamiliarWith
